
.home-aside {
	padding-left: 50px;
	font-size: 15px;
}
.recommend-title {
	padding: 0 0 10px;
	color: #999;
	font-size: 16px;
}
.recommend-articles-list {
	overflow: hidden;
	padding: 0;
	margin: 0;
}
.recommend-articles-list li {
	list-style: none;
	margin: 0;
	padding: 0;
	margin: 5px 0;
}
.ral-article {
	cursor: pointer;
	position: relative;
	padding: 10px 0;
}
a:hover .ralar-article-title,
a:hover .ralar-new-content {
	color: red;
}
.ralar-article-title {
	display: inline-block;
	width: 100%;
	line-height: 22px;
	height: 22px;
	font-weight: 600;
	color: #555;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.ralar-new-content {
	font-size: 12px;
	color: #777;
	display: inline-block;
	width: 100%;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.load-more-article button {
	margin-top: 20px;
	width: 100%;
	height: 34px;
	color: #999;
}



